import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

/* Services */
import { AccountService } from '../../Services/account.service';

@Component({
	selector: 'app-reset-password',
	templateUrl: './reset-password.component.html',
	styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

	form 		: any = {}
	pw_err 		: boolean
	new_pw_err 	: boolean
	confirm_err : boolean
	expired 	: boolean
	buttonText 	: string
	token 		: string
	token_err_m : string

	constructor(private AccountService: AccountService,
				private route: ActivatedRoute,
				private router: Router) {

	}

	ngOnInit() {
		this.form = {
			newPassword 	: '',
			confirmPassword : ''
		};
		this.buttonText = 'Save new password'
		this.initParams();
	}

	initParams() {
		this.route.params.subscribe((params: Params) => {
			this.token = params['token']
		});
	}

	validatePassword(form) {
		this.new_pw_err = this.pw_err = this.confirm_err = true;

		if (form.newPassword) {
			this.new_pw_err = form.newPassword.length >= 8 ? false : true
			this.confirm_err = form.newPassword === form.confirmPassword ? false : true
		}
		return (this.pw_err && this.confirm_err)
	}

	resetPassword() {
		if (!this.validatePassword(this.form) && this.token) {
			this.AccountService.resetPassword(this.token, { password: this.form.newPassword }).subscribe( res => {
				if (res.success)
					this.buttonText = 'Saved'
			}, error => {
				this.expired 		= true
				if (JSON.parse(error._body).error = "bad token")
					this.token_err_m 	= 'The security token included in the request is expired'
				else
					this.token_err_m 	= 'The security token is not valid'
				setTimeout( () => {
					this.router.navigate(['/']);
				}, 2000)
			});
		}
	}

}



